<template>
  <div class="main-div">
    <el-row type="flex"
            class="row-bg"
            justify="center">
      <el-card class="box-card main-card">
        <!-- 抓取表单 -->
        <el-form :model="mangaForm"
                 :rules="mangaRules"
                 ref="mangaForm"
                 label-width="100px"
                 class="demo-border">
          <!-- 目标站点 -->
          <el-form-item label="目标站点">
            <el-select v-model="mangaForm.targetSite"
                       placeholder="目标站点">
              <el-option label="轻之国度"
                         value="1"></el-option>
            </el-select>
          </el-form-item>
          <!-- 抓取方式 -->
          <el-form-item label="抓取方式">
            <el-radio-group @change="reptileTypeChange"
                            v-model="reptileType">
              <el-radio-button label="1">章节</el-radio-button>
              <el-radio-button label="2">漫画</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <!-- 目标地址 -->
          <el-form-item :label="targetUrlHint"
                        prop="targetUrl">
            <el-input v-model="mangaForm.targetUrl"></el-input>
          </el-form-item>
        </el-form>
        <!-- 提交 -->
        <div class="cmt-btn">
          <el-button type="primary"
                     plain
                     v-if="reptileType == 1"
                     @click="crawlChapter">解析章节</el-button>
          <el-button type="primary"
                     plain
                     v-if="reptileType == 2"
                     @click="crawlManga">解析漫画</el-button>
          <el-button type="primary"
                     plain
                     v-if="reptileType == 1"
                     @click="downloadChapter">下载章节</el-button>
          <el-button type="primary"
                     plain
                     v-if="reptileType == 2"
                     @click="downloadManga">下载漫画</el-button>
        </div>
        <!-- 结果预览 - 章节 -->
        <h1>{{chapterName}}</h1>
        <div class="block"
             v-for="item in chapterData"
             :key="item">
          <img :src="item"
               alt="">
        </div>
        <!-- 结果预览 - 漫画 -->
        <h1>{{mangaName}}</h1>
        <div v-for="item in mangaContent"
             :key="item.name">
          <h2>{{item.name}}</h2>
        </div>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials = true // 允许携带cookie
import { Loading } from 'element-ui'

let loadingInstance = null

export default {
  name: 'Main',
  created() {
    this.getSessionID()
  },
  data() {
    return {
      mangaForm: {
        targetSite: '1',
        targetUrl: '',
      },
      mangaRules: {},
      // websocket
      ws: null,
      wsMsg: '',
      // 章节图片地址
      chapterData: [],
      // 漫画章节标题
      mangaContent: [],
      // 章节名
      chapterName: '',
      // 漫画名
      mangaName: '',
      // 下载地址
      downloadUrl: '',
      // 抓取方式
      reptileType: '1',
      // 地址输入提示
      targetUrlHint: '章节地址',
    }
  },
  methods: {
    // 获取SessionID
    getSessionID: function () {
      let SessionID = ''
      axios.get('http://127.0.0.1/manga/connect').then((res) => {
        SessionID = res.data.data
        console.log(SessionID)
        this.openWebSocket(SessionID)
      })
    },
    // 建立ws连接
    openWebSocket: function (SessionID) {
      let _this = this
      let url = 'ws://127.0.0.1/websocket/' + SessionID
      this.ws = new WebSocket(url)
      this.ws.onopen = function () {
        console.log('尝试连接到服务器')
      }
      this.ws.onmessage = function (res) {
        this.wsMsg = res.data
        console.log(this.wsMsg)
        if (res.data.substring(0, 6) == 'wsMsg:') {
          loadingInstance.setText(res.data.substring(6))
        } else {
          console.log(res.data.substring(0, 6))
        }
      }
    },
    // 解析章节
    crawlChapter: function () {
      this.chapterData = []
      this.chapterName = ''
      this.mangaContent = []
      this.mangaName = ''
      let _this = this
      loadingInstance = Loading.service({
        lock: true,
        background: 'rgba(0, 0, 0, 0.7)',
        text: '章节解析中...',
      })
      axios
        .get('http://127.0.0.1/manga/detail', {
          params: { url: this.mangaForm.targetUrl },
        })
        .then((res) => {
          this.chapterData = res.data.data.images
          this.chapterName = res.data.data.name
          loadingInstance.close()
          this.successNotify(_this, '成功', '解析完毕')
        })
        .catch((err) => {
          console.log(err)
          loadingInstance.close()
          this.errorNotify(_this, '错误', '解析失败')
        })
    },
    // 解析漫画
    crawlManga: function () {
      this.chapterData = []
      this.chapterName = ''
      this.mangaContent = []
      this.mangaName = ''
      let _this = this
      loadingInstance = Loading.service({
        lock: true,
        background: 'rgba(0, 0, 0, 0.7)',
        text: '漫画解析中...',
      })
      axios
        .get('http://127.0.0.1/manga/chapters', {
          params: { url: this.mangaForm.targetUrl },
        })
        .then((res) => {
          this.mangaContent = res.data.data.chapters
          this.mangaName = res.data.data.name
          loadingInstance.close()
          this.successNotify(_this, '成功', '解析完毕')
        })
        .catch((err) => {
          console.log(err)
          loadingInstance.close()
          this.errorNotify(_this, '错误', '解析失败')
        })
    },
    // 下载章节
    downloadChapter: function () {
      let _this = this
      loadingInstance = Loading.service({
        lock: true,
        background: 'rgba(0, 0, 0, 0.7)',
        text: '获取图片中...',
      })
      axios
        .get('http://127.0.0.1/manga/downloadChapter', {
          params: {
            siteUrl: window.location.href.substring(
              0,
              window.location.href.length - 2
            ),
          },
        })
        .then((res) => {
          this.downloadUrl = res.data.data
          this.downloadUrl = this.downloadUrl.replace(/\[/g, '%5B')
          this.downloadUrl = this.downloadUrl.replace(/\]/g, '%5D')
          loadingInstance.close()
          this.successNotify(_this, '成功', '打包成功')
          window.open(this.downloadUrl, '_blank')
        })
        .catch((err) => {
          console.log(err)
          loadingInstance.close()
          this.errorNotify(_this, '错误', '获取失败')
        })
    },
    // 下载漫画
    downloadManga: function () {
      let _this = this
      loadingInstance = Loading.service({
        lock: true,
        background: 'rgba(0, 0, 0, 0.7)',
        text: '获取章节中...',
      })
      axios
        .get('http://127.0.0.1/manga/downloadManga', {
          params: {
            siteUrl: window.location.href.substring(
              0,
              window.location.href.length - 2
            ),
          },
        })
        .then((res) => {
          this.downloadUrl = res.data.data
          this.downloadUrl = this.downloadUrl.replace(/\[/g, '%5B')
          this.downloadUrl = this.downloadUrl.replace(/\]/g, '%5D')
          loadingInstance.close()
          this.successNotify(_this, '成功', '打包成功')
          window.open(this.downloadUrl, '_blank')
        })
        .catch((err) => {
          console.log(err)
          loadingInstance.close()
          this.errorNotify(_this, '错误', '获取失败')
        })
    },
    // 抓取方式改变
    reptileTypeChange: function (label) {
      if (label == 1) {
        this.targetUrlHint = '章节地址'
      } else if (label == 2) {
        this.targetUrlHint = '漫画地址'
      }
    },
    // 提示消息
    successNotify(_this, title, message) {
      _this.$notify({
        title: title,
        message: message,
        type: 'success',
      })
    },
    warningNotify(_this, title, message) {
      _this.$notify({
        title: title,
        message: message,
        type: 'warning',
      })
    },
    infoNotify(_this, title, message) {
      _this.$notify.info({
        title: title,
        message: message,
      })
    },
    errorNotify(_this, title, message) {
      _this.$notify.error({
        title: title,
        message: message,
      })
    },
  },
}
</script>

<style scoped>
.main-card {
  margin-top: 150px;
  min-width: 60%;
}
.main-div {
  background-color: rgba(0, 0, 0, 0.562);
  height: 100%;
}
.cmt-btn {
  text-align: center;
}
</style>



